.App {
  text-align: center;
}

.main{
  display: flex;
  flex-direction: row;
  align-items: center;
  place-items: center;
  justify-content: center;
  padding: 3em;
  gap: 2em;
}

.main1{
  background-color: rgb(0, 204, 255);
}

.cart{
  padding-top: 2em;
  padding-right: 3em;
  text-align: right;
}



.Button1{
  display: flex;
  justify-content: center;
}

.main .card:hover {
  margin-top: -.25rem;
  margin-bottom: .25rem;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.3);
}

.card1{
  text-align: left;
  padding-left: 1em;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
